	export default {
        onLoad() {
            this.list = []
            this.getList()
        },
		data() {
			return {
				list:[],
                page: 1
			}
		},
		methods: {
            getList(){
                uni.showLoading({
                    title: '正在加载中...',
                    mask: false
                });
                uni.request({
                    url: 'https://www.apiopen.top/novelApi', //仅为示例，并非真实接口地址。
                    data: {
                        page: this.page
                    },
                    header: {
                        // 'custom-header': 'hello' //自定义请求头信息
                    },
                    success: (res) => {
                        // console.log(res.data);
                        uni.hideLoading();
                        if(res.data.code===200){
                            this.list.push(...res.data.data);
                        }
                    }
                });
            },
            getDetail(item){
                uni.navigateTo({
                    url:'../novel-detail/novel-detail?name=' + item.bookname
                })
            }
        }
	}
